body {
  margin: 0;
  box-sizing: border-box; }

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%; }

.Header {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center; }
  .Header-context {
    display: flex;
    width: 100%;
    align-items: center; }
    .Header-context__text {
      display: inline-block; }
    .Header-context__typewriter {
      font-family: Arial, Helvetica, sans-serif;
      font-size: 5rem;
      font-weight: bold; }
    .Header-context__intro {
      font-family: Arial, Helvetica, sans-serif;
      font-size: 2rem;
      font-weight: bold; }
    .Header-context__gif {
      max-width: 680px;
      max-height: 428px;
      width: 50%; }
  .Header-repos {
    font-size: 3rem; }

.App {
  width: 100%;
  height: 100vh;
  background-color: #fff;
  margin: 0 auto;
  padding: 5vh 20rem;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .App__navbar {
    height: 10vh; }
  .App__content {
    height: 85vh; }
  .App__footer {
    height: 5vh; }

.Navbar {
  display: flex;
  width: 100%;
  margin-bottom: 5rem;
  align-items: center; }
  .Navbar__logo {
    margin-right: auto;
    margin-left: 5rem;
    height: auto;
    width: auto; }
  .Navbar__links {
    justify-self: flex-end;
    margin-right: 5rem; }
  .Navbar-Link {
    margin-right: 3rem; }
  .Navbar-icons {
    width: 3rem;
    height: 3rem; }

.Footer {
  display: flex;
  width: 100%;
  margin-top: 5rem;
  justify-content: space-between;
  font-size: 6rem; }
  .Footer__html5 {
    color: #ff4800; }
  .Footer__css3 {
    color: #0667c2; }
  .Footer__sass {
    color: #d35187; }
  .Footer__js {
    color: yellow;
    background-color: black; }
  .Footer__react {
    color: #00c3ff; }
  .Footer__npm {
    color: #ca0404; }
  .Footer__git {
    color: #ff3300; }

